body {
  font-family: Arial, Helvetica, sans-serif;
}
.postWrapper {
  display: grid;
  background-color: #003152;
  width: 700px;
  height: 400px;
  margin: 20px auto;
  padding: 15px;
  border-radius: 10px;
  grid-template-columns: 1fr 6fr;
  grid-template-rows: 1fr 1fr 4fr;
}
.authorImage {
  width: 80px;
  height: 80px;
  border-radius: 80px;
  grid-column: 1/2;
  grid-row: 1/3;
}
.authorInfo {
  grid-column: 2/3;
  grid-row: 1/2;
}
.contentText {
  grid-column: 2/3;
  grid-row: 2/3;
  color: white;
  padding-left: 5px;
  font-size: 22px;
}
.contentImage {
  width: 600px;
  height: 300px;
  padding-bottom: 20px;
  padding-right: 10px;
  border-radius: 30px;
  grid-column: 2/3;
  grid-row: 3/4;
}
.authorName {
    color: white;
  padding-left: 5px;
  font-weight: 600;
  font-size: 18px;
}
.authorNickname, .date {
    color: #888888;
    padding-left: 10px;
    font-weight: 600;
    font-size: 18px;
}